import React, { Component, FunctionComponent, useState } from 'react';

import styles from './Header.module.scss';

type Props = {
  siteTitle: string;
  items: any[];
  activeGroup: number;
};

type State = {
  isOpened: boolean;
};

class Header extends Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      isOpened: false,
    };
  }

  render() {
    const { items, activeGroup } = this.props;

    const rootStyles = [styles.root];
    if (this.state.isOpened) rootStyles.push(styles.isOpened);

    return (
      <header className={rootStyles.join(' ')}>
        <nav className={styles.navbar}>
          <div className={styles.navbarBrand}>
            <a href="/">Krzysztof Grzybowski</a>
          </div>
          <div className={styles.navbarMain}>
            <ul className={styles.navbarNav}>
              {items.map(({ node }, index) => {
                const group = items[index].node.frontmatter.group;

                if (node.frontmatter.slug !== 'contact' && node.frontmatter.slug !== 'photo') {
                  const navItemStyles = [styles.navItem];
                  if (group === activeGroup) navItemStyles.push(styles.active);

                  return (
                    <li key={node.id} className={navItemStyles.join(' ')}>
                      <a
                        href={`#${node.frontmatter.slug}`}
                        onClick={e => {
                          e.preventDefault();
                          const element = document.getElementById(
                            node.frontmatter.slug
                          );
                          if (element) {
                            element.scrollIntoView({
                              behavior: 'smooth',
                              block: 'end',
                              inline: 'start',
                            });
                          }
                        }}
                      >
                        {node.frontmatter.title}
                      </a>
                    </li>
                  );
                }
              })}
            </ul>
            {items.map(({ node }, index) => {
              const group = items[index].node.frontmatter.group;

              if (node.frontmatter.slug === 'contact') {
                const navbarContactStyles = [styles.navbarContact];
                if (group === activeGroup) navbarContactStyles.push(styles.active);

                return (
                  <div key={node.id} className={styles.navbarContactWrapper}>
                    <a
                      href={`#${node.frontmatter.slug}`}
                      className={navbarContactStyles.join(' ')}
                      onClick={e => {
                        e.preventDefault();
                        const element = document.getElementById(
                          node.frontmatter.slug
                        );
                        if (element) {
                          element.scrollIntoView({
                            behavior: 'smooth',
                            block: 'end',
                            inline: 'start',
                          });
                        }
                      }}
                    >
                      {node.frontmatter.title}
                    </a>
                  </div>
                );
              }
            })}
          </div>
        </nav>
        <button
          type="button"
          className={styles.navbarToggle}
          onClick={() => {
            if (this.state.isOpened) {
              this.setState({ isOpened: false });
              document.body.classList.remove('overflow-hidden');
            } else {
              this.setState({ isOpened: true });
              document.body.classList.add('overflow-hidden');
            }
          }}
        >
          {this.state.isOpened ? (
            <i
              className={['fas fa-times', styles.navbarToggleIcon].join(' ')}
            ></i>
          ) : (
            <i
              className={['fas fa-bars', styles.navbarToggleIcon].join(' ')}
            ></i>
          )}
        </button>
      </header>
    );
  }
}
export default Header;
