import React, { Component, ReactNode, MouseEvent, createRef } from 'react';
import { TransitionGroup, CSSTransition } from 'react-transition-group';
import transitionEnd from 'dom-helpers/transitionEnd';

import styles from './Carousel.module.scss';

import CarouselItem from './CarouselItem';

type Props = {
  children: ReactNode[];
  delay: number;
  rootClassName?: string;
  itemsClassName?: string;
  controlClassName?: string;
  controlsClassName?: string;
};

type State = {
  activeIndex: number;
  maxHeight: number;
};

class Carousel extends Component<Props, State> {
  numberOfChildren: number;
  interval: number;
  element: HTMLElement | null;
  maxHeight: number;

  constructor(props: Props) {
    super(props);

    this.state = {
      activeIndex: 0,
      maxHeight: 0,
    };

    this.numberOfChildren = React.Children.toArray(this.props.children).filter(
      React.isValidElement
    ).length;
    this.interval = 0;
    this.element = null;
    this.maxHeight = 0;
  }

  componentDidMount() {
    const { delay } = this.props;
    this.interval = window.setInterval(
      document.visibilityState ? this.nextWhenVisible : this.next,
      delay
    );
    window.addEventListener('resize', this.onResize);
  }

  componentWillUnmount() {
    window.clearInterval(this.interval);
    window.removeEventListener('resize', this.onResize);
  }

  onResize = () => {
    this.setState(
      {
        maxHeight: 0,
      },
      this.calculateMaxHeight
    );
  };

  next = () => {
    const { activeIndex } = this.state;

    const nextIndex = activeIndex + 1;

    if (nextIndex <= this.numberOfChildren - 1) {
      this.setState({ activeIndex: nextIndex });
    } else {
      this.setState({ activeIndex: 0 });
    }
  };

  nextWhenVisible = () => {
    if (!document.hidden) {
      this.next();
    }
  };

  onClickControl = (index: number) => (e: MouseEvent) => {
    window.clearInterval(this.interval);

    this.setState({ activeIndex: index });
  };

  calculateMaxHeight = () => {
    if (this.element) {
      const children = Array.from(this.element.childNodes);
      children.forEach(child => {
        const grandchildren = Array.from((child as HTMLElement).childNodes);
        const height = (grandchildren[0] as HTMLElement).offsetHeight;
        if (height > this.state.maxHeight) {
          this.setState({
            maxHeight: height,
          });
        }
      });
    }
  };

  render() {
    const {
      children,
      rootClassName,
      itemsClassName,
      controlClassName,
      controlsClassName,
    } = this.props;
    const { activeIndex } = this.state;

    const rootStyles = [styles.root];
    if (rootClassName) rootStyles.push(rootClassName);

    const itemsStyles = [styles.items];
    if (itemsClassName) itemsStyles.push(itemsClassName);

    const controlsStyles = [styles.controls];
    if (controlsClassName) controlsStyles.push(controlsClassName);

    return (
      <>
        <div className={rootStyles.join(' ')}>
          <ul className={controlsStyles.join(' ')}>
            {children.map((child, index) => {
              const isActive = index === activeIndex;

              const controlStyles = [styles.control];
              if (controlClassName) controlStyles.push(controlClassName);
              if (isActive) controlStyles.push(styles.active);

              return (
                <li key={index} className={controlStyles.join(' ')}>
                  <button
                    type="button"
                    className={styles.controlButton}
                    onClick={this.onClickControl(index)}
                  >
                    <span className="sr-only">{index}</span>
                  </button>
                </li>
              );
            })}
          </ul>
          <div
            className={itemsStyles.join(' ')}
            style={{
              height: this.state.maxHeight,
            }}
            ref={element => {
              if (element) {
                this.element = element;
                this.calculateMaxHeight();
              }
            }}
          >
            {children.map((child, index) => {
              const isActive = index === activeIndex;

              return (
                <CSSTransition
                  key={index}
                  in={isActive}
                  addEndListener={transitionEnd}
                  appear
                  className={styles.item}
                  classNames={{
                    appear: styles.appear,
                    appearActive: styles.appearActive,
                    appearDone: styles.appearDone,
                    enter: styles.enter,
                    enterActive: styles.enterActive,
                    enterDone: styles.enterDone,
                    exit: styles.exit,
                    exitActive: styles.exitActive,
                    exitDone: styles.exitDone,
                  }}
                  // mountOnEnter
                  // unmountOnExit
                >
                  <div>{child}</div>
                </CSSTransition>
              );
            })}
          </div>
        </div>
      </>
    );
  }
}

export default Carousel;
