import React, {
  FunctionComponent,
  Component,
  useState,
  useCallback,
  Fragment,
} from 'react';
import { Link } from 'gatsby';
import { useStaticQuery, graphql } from 'gatsby';
import { cloneDeep } from 'lodash';

import styles from './index.module.scss';

import Header from '../components/header/header';
import Layout from '../components/layout/Layout';
import SEO from '../components/seo';

import Section from '../components/section/Section';
import Photo from '../components/photo/photo';
import Biography from '../components/biography/Biography';
import VideoTile from '../components/video/Video';
import AudioTile from '../components/audio/Audio';
import Recommendations from '../components/recommendations/Recommendations';
import Reviews from '../components/reviews/Reviews';
import Gallery from '../components/gallery/Gallery';
import Contact from '../components/contact/Contact';

import Modal from '../components/modal/Modal';
import AudioPlayer from '../components/player/Player';

type Props = {
  data: any;
};

type State = {
  activeIndex: number;
  activeGroup: number;
  lastActiveIndex: number;
  videoModal: {
    isShown: boolean;
    videoId: null | string;
  };
  audioPlayer: {
    name: string;
    file: string;
  } | null;
};

class IndexPage extends Component<Props, State> {
  blocks: any[];
  mounted: boolean;

  constructor(props: any) {
    super(props);
    this.state = {
      activeIndex: 0,
      activeGroup: 0,
      lastActiveIndex: 0,
      videoModal: {
        isShown: false,
        videoId: null,
      },
      audioPlayer: null,
    };
    this.blocks = [];
    this.mounted = false;
  }

  componentDidMount() {
    const { content } = this.props.data;

    this.mounted = true;
    let activeIndex = 0;
    let lastActiveIndex = 0;

    window.addEventListener('scroll', e => {
      for (let i = 0; i < this.blocks.length; i++) {
        activeIndex = i;

        if (
          window.scrollY <
          this.blocks[i].current.offsetTop + this.blocks[i].current.offsetHeight
        ) {
          break;
        }
      }

      for (let j = 0; j < this.blocks.length; j++) {
        lastActiveIndex = j;

        if (
          window.scrollY <
          this.blocks[j].current.offsetTop +
            this.blocks[j].current.offsetHeight -
            window.innerHeight * 0.7
        ) {
          break;
        }
      }

      if (activeIndex !== this.state.activeIndex) {
        this.setState({ activeIndex });
      }

      const group = content.edges[activeIndex].node.frontmatter.group;

      if (group !== this.state.activeGroup) {
        this.setState({ activeGroup: group });
      }

      if (lastActiveIndex > this.state.lastActiveIndex) {
        this.setState({ lastActiveIndex });
      }
    });
  }

  getRef = (ref: any, i: any) => {
    this.blocks[i] = ref;
  };

  render() {
    if (this.mounted) {
    }
    const { lastActiveIndex, activeGroup, audioPlayer } = this.state;

    const {
      site,
      content,
      videos,
      audios,
      recommendations,
      reviews,
      gallery,
    } = this.props.data;

    return (
      <>
        <Header
          siteTitle={site.siteMetadata.title}
          items={content.edges}
          activeGroup={activeGroup}
        />
        <Layout>
          <SEO title="Home" />
          {content.edges.map(({ node }: any, i: any) => {
            if (node.frontmatter.slug === 'photo') {
              return (
                <Section
                  key={node.id}
                  index={i}
                  getRef={this.getRef}
                  shown={i <= lastActiveIndex}
                  animation="fadeIn"
                >
                  <Photo src={node.frontmatter.file.publicURL} />
                </Section>
              );
            }

            if (node.frontmatter.slug === 'biography') {
              return (
                <Section
                  key={node.id}
                  id={node.frontmatter.slug}
                  index={i}
                  getRef={this.getRef}
                  shown={i <= lastActiveIndex}
                >
                  <Biography content={node.html} />
                </Section>
              );
            }

            if (node.frontmatter.slug === 'videos') {
              return (
                <Section
                  key={node.id}
                  id={node.frontmatter.slug}
                  index={i}
                  getRef={this.getRef}
                  shown={i <= lastActiveIndex}
                >
                  <div
                    className={['container-fluid', styles.container].join(' ')}
                  >
                    <div
                      className={['row no-gutters', styles.videosRow].join(' ')}
                    >
                      {videos.edges.map(({ node }: any) => {
                        return (
                          <div
                            key={node.id}
                            className={['col-lg-4', styles.videoCol].join(' ')}
                          >
                            <VideoTile
                              title={node.frontmatter.title}
                              videoId={node.frontmatter.videoId}
                              coverUrl={node.frontmatter.cover.publicURL}
                              onClick={e => {
                                e.preventDefault();
                                const newState = cloneDeep(this.state);
                                newState.videoModal.videoId =
                                  node.frontmatter.videoId;
                                newState.videoModal.isShown = true;
                                this.setState(newState);
                              }}
                            />
                          </div>
                        );
                      })}
                    </div>
                  </div>
                </Section>
              );
            }

            if (node.frontmatter.slug === 'audios') {
              return (
                <Section
                  key={node.id}
                  id={node.frontmatter.slug}
                  index={i}
                  getRef={this.getRef}
                  shown={i <= lastActiveIndex}
                >
                  <div className={styles.audios}>
                    <div className="container-fluid">
                      <div className={['row', styles.audiosRow].join(' ')}>
                        {audios.edges.map(({ node }: any) => {
                          return (
                            <div
                              key={node.id}
                              className={[
                                styles.audioCol,
                                'col-6 col-md-4 col-lg-3 col-xl',
                              ].join(' ')}
                            >
                              <AudioTile
                                name={node.frontmatter.title}
                                file={node.frontmatter.file.publicURL}
                                onPlay={(name, file) => {
                                  const audioPlayer = {
                                    name,
                                    file,
                                  };
                                  this.setState({
                                    audioPlayer,
                                  });
                                }}
                                isPlaying={
                                  node.frontmatter.title === audioPlayer?.name
                                }
                              />
                            </div>
                          );
                        })}
                      </div>
                    </div>
                  </div>
                </Section>
              );
            }

            if (node.frontmatter.slug === 'recommendations') {
              return (
                <Section
                  key={node.id}
                  id={node.frontmatter.slug}
                  index={i}
                  getRef={this.getRef}
                  shown={i <= lastActiveIndex}
                >
                  <Recommendations
                    data={recommendations.edges}
                    shown={i <= lastActiveIndex}
                  />
                </Section>
              );
            }

            if (node.frontmatter.slug === 'reviews') {
              return (
                <Section
                  key={node.id}
                  id={node.frontmatter.slug}
                  index={i}
                  getRef={this.getRef}
                  shown={i <= lastActiveIndex}
                >
                  <Reviews data={reviews.edges} />
                </Section>
              );
            }

            if (node.frontmatter.slug === 'gallery') {
              return (
                <Section
                  key={node.id}
                  id={node.frontmatter.slug}
                  index={i}
                  getRef={this.getRef}
                  shown={i <= lastActiveIndex}
                >
                  {/* <Photo src={node.frontmatter.backgroundImage.publicURL} /> */}
                  <Gallery data={gallery.edges} />
                </Section>
              );
            }

            if (node.frontmatter.slug === 'contact') {
              return (
                <Section
                  key={node.id}
                  id={node.frontmatter.slug}
                  index={i}
                  getRef={this.getRef}
                  shown={i <= lastActiveIndex}
                  animation="fadeIn"
                >
                  <Contact />
                </Section>
              );
            }
          })}
        </Layout>
        {audioPlayer && (
          <AudioPlayer
            name={audioPlayer.name}
            file={audioPlayer.file}
            onClose={() => {
              this.setState({
                audioPlayer: null,
              });
            }}
          />
        )}
        {this.state.videoModal.isShown && (
          <Modal
            onHide={() => {
              const newVideoModal = cloneDeep(this.state.videoModal);
              newVideoModal.isShown = false;
              this.setState({
                videoModal: newVideoModal,
              });
            }}
          >
            <div className="embed-responsive embed-responsive-16by9">
              <iframe
                className="embed-responsive-item"
                src={`https://www.youtube.com/embed/${this.state.videoModal.videoId}`}
                allowFullScreen
              ></iframe>
            </div>
          </Modal>
        )}
      </>
    );
  }
}

export default (() => {
  const data = useStaticQuery(graphql`
    query IndexMarkdown {
      site {
        siteMetadata {
          title
        }
      }

      content: allMarkdownRemark(
        filter: { fileAbsolutePath: { regex: "//home//" } }
        sort: { fields: [frontmatter___order], order: ASC }
      ) {
        edges {
          node {
            fileAbsolutePath
            frontmatter {
              slug
              title
              file {
                publicURL
              }
              group
            }
            html
          }
        }
      }

      videos: allMarkdownRemark(
        filter: { fileAbsolutePath: { regex: "//videos//" } }
      ) {
        edges {
          node {
            fileAbsolutePath
            frontmatter {
              title
              cover {
                publicURL
              }
              videoId
            }
          }
        }
      }

      audios: allMarkdownRemark(
        filter: { fileAbsolutePath: { regex: "//audios//" } }
        sort: { fields: [frontmatter___order], order: ASC }
      ) {
        edges {
          node {
            fileAbsolutePath
            frontmatter {
              title
              file {
                publicURL
              }
            }
          }
        }
      }

      recommendations: allMarkdownRemark(
        filter: { fileAbsolutePath: { regex: "//recommendations//" } }
      ) {
        edges {
          node {
            fileAbsolutePath
            frontmatter {
              source
            }
            html
          }
        }
      }

      reviews: allMarkdownRemark(
        filter: { fileAbsolutePath: { regex: "//reviews//" } }
      ) {
        edges {
          node {
            fileAbsolutePath
            frontmatter {
              title
              source
              date
            }
            html
          }
        }
      }

      gallery: allFile(filter: { absolutePath: { regex: "//gallery//" } }) {
        edges {
          node {
            publicURL
          }
        }
      }
    }
  `);

  return <IndexPage data={data} />;
}) as FunctionComponent;
